import axios, {
  AxiosError,
  AxiosInstance,
  AxiosResponse,
  InternalAxiosRequestConfig,
} from "axios";
import { getToken, refreshToken } from "@/services/AuthService";
import { BASE_URL_SECRET } from "@/secret";


const BASE_URL = BASE_URL_SECRET;

const apiClient: AxiosInstance = axios.create({
  baseURL: BASE_URL,
  headers: { "Content-Type": "application/json" },
  timeout: 10000,
});

// ----- Request interceptor -----
apiClient.interceptors.request.use(
  (config: InternalAxiosRequestConfig) => {
    const token = getToken();
    if (token) {
      if (config.headers?.set) {
        // When AxiosHeaders
        config.headers.set("Authorization", `Bearer ${token}`);
      } else {
        // When it's still a POJO (depends on build tooling)
        (config.headers as any) = {
          ...config.headers,
          Authorization: `Bearer ${token}`,
        };
      }
    }
    return config;
  },
  (error: AxiosError) => Promise.reject(error)
);

// ----- Response interceptor (includes refresh retry w/ _retry guard) -----
apiClient.interceptors.response.use(
  (response: AxiosResponse) => response,
  async (error: AxiosError) => {
    const { response, config } = error;
    if (response?.status === 401 && config && !(config as any)._retry) {
      (config as any)._retry = true; // avoid infinite loop
      try {
        const newToken = await refreshToken();
        if (newToken) {
          if (config.headers?.set) {
            config.headers.set("Authorization", `Bearer ${newToken}`);
          } else {
            (config.headers as any).Authorization = `Bearer ${newToken}`;
          }
          return apiClient(config as InternalAxiosRequestConfig);
        }
      } catch (refreshError) {
        console.error("Token refresh failed:", refreshError);
      }
    }
    return Promise.reject(error);
  }
);

export default apiClient;
